import { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import { EngLoc } from "../../assets/constants/localizations";
import { Text, Title3 } from "../../assets/styles/component";
import Button from "../../assets/UI/ButtonDesktop";

const SService = styled.div`
  display: flex;
  flex-direction: column;
  text-align: left;
  gap: 0.938rem;
  padding: 3.063rem;
  width: 50%;
  max-width: 600px;

  background: linear-gradient(90.7deg, #f4faff 8.32%, #fbfdff 99.04%), #0079e7;
  border-radius: 2.75rem;

  & button {
    background: radial-gradient(
      105.43% 294.48% at -10.2% -116.67%,
      #376ce0 0%,
      #2485dd 25.96%,
      #4f4ed9 74.99%,
      #7320cf 96.87%
    );
    box-shadow: inset 0 0 37px -11px rgba(255, 255, 255, 0.29);
    border-radius: 34.5px;
  }

  &:first-child {
    & > div {
      gap: 0;
      justify-content: flex-start;
    }

    img {
      width: 113px;
      height: 113px;

      &:not(:first-child) {
        margin-left: -35px;

        @media (max-width: 1400px) and (min-width: 1200px) {
          margin-left: -65px;
        }
      }
    }
  }

  @media (max-width: 1200px) {
    width: 100%;
  }
`;

const Images = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  height: 244px;
  gap: 1.25rem;

  overflow: hidden;
`;

const Title = styled(Title3)`
  display: flex;
  gap: 1.5rem;

  font-size: clamp(39px, 4vw, 54px);
  font-weight: 400;
  line-height: 130%;
  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;

  margin-top: 4.688rem;
`;

const SText = styled(Text)`
  margin-bottom: 2.188rem;
`;

const Img = styled.img`
  width: 100%;
  opacity: 0; 

  ${({ view, index }) =>
    !view &&
    `
      animation: 1s 0.${index}s ease-in-out smooth_left forwards;

      @keyframes smooth_left {
        0% {
          opacity: 0;
          transform: translateX(-100px);
        }
        100% {
          opacity: 1;
          transform: translateX(0);
        }
      }
    `}
`;

const Service = ({ title, desc, btnText, imgs }) => {
  const [view, setView] = useState(true);
  const elem = useRef(null);

  const intersectionObserver = new IntersectionObserver((entries) => {
    if (entries[0].intersectionRatio <= 0) return;
    setView(false);
  });

  useEffect(() => {
    intersectionObserver.observe(elem.current && elem.current);
  }, []);

  return (
    <SService>
      <Images>
        {imgs.map((img, index) => (
          <Img
            ref={elem}
            view={view}
            index={index}
            key={index}
            width={img.width}
            height={img.height}
            loading="lazy"
            alt="Service_img"
            src={img.img}
          />
        ))}
      </Images>
      <Title>{title}</Title>
      <SText size="20" color="#3C3C3C;">
        {desc}
      </SText>
      <Button type="service" colorTxt="#E7F5FF" link={true}>
        {btnText}
      </Button>
    </SService>
  );
};

export default Service;
